body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background-color: black;
  color: white;
}

.content {
  padding: 60px 150px;
}

.row {
  display: flex;

  &--actions {
    margin-top: 40px;
  }

  &--centered {
    align-items: center;
  }
}

.column {
  display: flex;
  flex-direction: column;
  width: 100%;

  &--left {
    align-items: flex-start;
  }

  &--right {
    align-items: flex-end;
  }

  &--centered {
    align-items: center;
  }

}

.profile {
  &__info {
    font-size: 40px;
  }

  &__title {
    font-size: 60px;
  }

  &__payment-info {
    font-size: 25px;
    margin-bottom: 15px;
    padding: 0px;
  }

  &__payment-table {
    width: 100%;
    border-collapse: collapse;

    * {
      border: 1px solid grey;
    }

    th {
      background-color: grey;
    }

    td {
      background-color: lightgrey;
      color: black;
      font-weight: 600;
    }
  }

  &__button {
    width: 60%;
    height: 100px;
    background-color: gray;
    color: white;
    border: none;
    font-size: 25px;
    
    &:not(:last-child) {
      margin-bottom: 30px;
    }

    &--warning {
      outline: 5px solid red;
    }
  }
}


.modal {
  color: black;

  &__text {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100px;
    margin-bottom: 30px;
    font-size: 35px;
  }

  &__buttons-row {
    display: flex;
    justify-content: space-around;
    margin-bottom: 20px;
  }

  &__modal-btn {
    background-color: grey;
    border: none;
    width: 200px;
    color: white;
    font-weight: 600;
    height: 100px;
    font-size: 20px;
  }
}


button:focus {
  background-color: green;
}

button.disabled {
  pointer-events: none;
}